<script lang="ts">
import { defineComponent } from 'vue';
import { Employee } from '@/model';

export default defineComponent({
  name: 'EmployeeView',
  data() {
    return {
      loading: true,
      employee: {} as Employee,
    }
  },
  computed: {
    employeeId() {
      return Number(this.$route.params.id);
    },
  },
  watch: {
    employeeId() {
      this.loadEmployee();
    }
  },
  mounted() {
    this.loadEmployee();
  },
  methods: {
    loadEmployee() {
      this.$api.employee.get(this.employeeId).then((employee) => {
        this.employee = employee;
        this.loading = false;
      });
    }
  }
});
</script>

