import axios, { AxiosInstance } from 'axios';

export interface IBaseApi {
  client: AxiosInstance;
  resource: string;
}

export interface IReadOnlyApi<T> extends IBaseApi {
  list: () => Promise<T[]>;
  get: (id: number) => Promise<T>;
}

export interface IReadWriteApi<T> extends IReadOnlyApi<T> {
  create: (data: T) => Promise<T>;
  update: (id: number, data: T) => Promise<T>;
  delete: (id: number) => Promise<any>;
}

export class BaseApi implements IBaseApi {
  client: AxiosInstance;
  resource: string;

  constructor(baseURL: string, resource: string) {
    this.client = axios.create({ baseURL });
    this.resource = resource;
  }
}

export class ReadOnlyApi<T> extends BaseApi implements IReadOnlyApi<T> {
  async list(): Promise<T[]> {
    return this.client
      .get(this.resource)
      .then((resp) => resp.data);
  }

  async get(id: number): Promise<T> {
    return this.client
      .get(`${this.resource}/${id}`,)
      .then((resp) => resp.data);
  }
}

export class ReadWriteApi<T> extends ReadOnlyApi<T> implements IReadWriteApi<T> {
  async create(data: T): Promise<T> {
    return this.client
      .post(this.resource, data)
      .then((resp) => resp.data);
  }

  async update(id: number, data: T): Promise<T> {
    return this.client
      .put(`${this.resource}/${id}`, data)
      .then((resp) => resp.data);
  }

  async delete(id: number): Promise<any> {
    return this.client
      .delete(`${this.resource}/${id}`)
      .then((resp) => resp.data);
  }
}
