import { createApp } from 'vue';

// App specific
import App from './App.vue';
import Api from '@/plugins/api';
import router from './router';

const BASE_URI = `${window.location.protocol}//${document.location.host}`;

const app = createApp(App);

app.use(router)
app.use(Api, {
  baseUrl: `${BASE_URI}/xyzapi`,
});

app.mount('#app');
